import React from "react"
import { Modal as DraggableModal } from "react-draggable-modal"

import 'react-draggable-modal/lib/modal.css';
import "./index.css"

const Modal = ({ children, open, onClose }) => {
    return (
        <DraggableModal visible={open} width="700px" height="500px" mask={false} onOk={onClose} onCancel={onClose} >
            {
                children
            }
        </DraggableModal>
    )
}

export default Modal