import React, { useRef, useState } from "react";
import ProductItem from "./ProductItem";
import ProductItemDetail from "./ProductItemDetail";
import Modal from "../Modal";
import styles from "./index.module.css";
import PropTypes from "prop-types";
import { simpleDragAndDrop } from "../../utils";
import data from "../../data.json";

const Products = props => {
  const { dndPermission } = { ...props };
  const projectsWrapper = useRef(null);
  const [selectedProduct, setSelectedProduct] = useState({})

  const [modalOpen, setModalOpen] = useState(false)

  const handleModalOpen = () => setModalOpen(true)
  const handleModalclose = () => setModalOpen(false)

  const handleProductItemClick = (product) => {
    handleModalOpen()
    setSelectedProduct(product)
  }

  return (
    <div>
      <div className={styles.projectsWrapper} ref={projectsWrapper}>
        <div
          className={styles.projectsBorderTop}
          onMouseDown={e => {
            if(window.innerWidth >= 976) {
              simpleDragAndDrop(projectsWrapper.current, e, dndPermission);
            }
          }}
        />
        <div className={styles.projects}>
          <div
            className={styles.projectsText}
            onMouseDown={e => {
              if(window.innerWidth >= 976) {
                simpleDragAndDrop(projectsWrapper.current, e, dndPermission);
              }
            }}
          >
            STORM PROJECTS
          </div>
          <div className={styles.projectsList}>
            {data.products.map((element, index) => {
              return (
                <ProductItem name={element.name} logo={element.logo} key={index} onClick={() => handleProductItemClick(element)} />
              );
            })}
          </div>
        </div>
      </div>
      <Modal open={modalOpen} onClose={handleModalclose}>
        <ProductItemDetail product={selectedProduct} />
      </Modal>
    </div>
  );
};

Products.propTypes = {
  dndPermission: PropTypes.string.isRequired
};

export default Products;
