import React from "react"
import styles from "./index.module.css"

const ProductItem = ({ name, link, logo, onClick }) => {
    return (
        <div className={styles.productItem} onClick={onClick}>
            {/* <img className={styles.productItemLogo} src={logo} alt={name} /> */}
            <div className={styles.productTitle}>
                <h3>{ name }</h3>
            </div>
        </div>
    )
}

export default ProductItem