import React, { useState, useEffect, useRef } from "react";
import { Animator } from "@arwes/react-animator";
import { GridLines, Dots, MovingLines } from "@arwes/react-bgs";
import Modal from "../Modal";
import ScifiLoader from "../Loader";
import Terminal from "../Terminal";
import HackingFbi from "../HackingFbi";
import Projects from "../Projects";
import Products from "../Products";
import SkillChart from "../SkillChart";
// import CV from "../CV"
import ilyaSvg from "../../img/ilyahacker.svg";
import Footer from "../Footer";
import { simpleDragAndDrop } from "../../utils";
import styles from "./index.module.css";

const App = () => {
  const [preHacked, setPreHacked] = useState(false);
  const [hacked, setHacked] = useState(false);
  const [dndPermission, setDndPermission] = useState("granted");
  const ilyahacker = useRef(null);

  useEffect(() => {
    if (window.innerWidth < 975) {
      setHacked(true);
      setDndPermission("denied");
    }
  });

  return (
    <Animator active={true} duration={{ enter: 2, exit: 2 }} >
      <div className={styles.app}>
        <div
          style={{
            position: "absolute",
            zIndex: 0,
            left: 0,
            right: 0,
            top: 0,
            bottom: 0,
            backgroundColor: "#000906",
            backgroundImage:
              "radial-gradient(85% 85% at 50% 50%, hsla(185, 100%, 25%, 0.25) 0%, hsla(185, 100%, 25%, 0.12) 50%, hsla(185, 100%, 25%, 0) 100%)",
          }}
        >
          <GridLines lineColor="hsla(180, 100%, 75%, 0.05)" distance={30} />
          <Dots color="hsla(180, 100%, 75%, 0.05)" distance={30} />
          <MovingLines
            lineColor="hsla(180, 100%, 75%, 0.07)"
            distance={30}
            sets={20}
          />
        </div>
        <div className={styles.refresh}></div>
        <div className={styles.cligno}></div>

        <Terminal setPreHacked={setPreHacked} auto={false} />
        <ScifiLoader />
        {preHacked && (
          <div>
            <HackingFbi setHacked={setHacked} hacked={hacked} />
            <SkillChart />
          </div>
        )}

        {hacked && (
          <div>
            <img
              src={ilyaSvg}
              alt=""
              className={styles.ilyahacker}
              ref={ilyahacker}
              onMouseDown={(e) => {
                if(window.innerWidth >= 976) {
                  simpleDragAndDrop(ilyahacker.current, e, dndPermission);
                }
              }}
            />

            <div className={styles.footerMobile}>
              <Footer setPreHacked={setPreHacked} />
            </div>
            {/* <CV dndPermission={dndPermission} /> */}
            <Products dndPermission={dndPermission} />
            <Projects dndPermission={dndPermission} />
          </div>
        )}
        <div className={styles.footerDesktop}>
          <Footer setPreHacked={setPreHacked} />
        </div>
      </div>
      <Modal />
    </Animator>
  );
};

export default App;
