import React from "react"
import styles from "./index.module.css"

const ProductItemDetail = ({ product }) => {
    return (
        <div className={styles.productDetailContainer}>
            <div className={styles.productDetailName}>
                <h1>{product.name}</h1>
            </div>
            <div className={styles.productDetailImage}>
                <img src={product.image} alt={product.name} draggable={false} />
            </div>
            <div className={styles.productDetailLink}>
                <a href={product.href} target="_blink">
                    <img src={product.logo} alt={product.name} className={styles.productDetailLogo} />
                </a>
            </div>
            <div className={styles.productDescription}>
                <p>
                    { product.description }
                </p>
            </div>
            <div className={styles.productTopic}>
                <div className={styles.productTopicHeader}>
                    <h2>Topic</h2>
                </div>
                <div className={styles.productTopicBody}>
                    {
                        !!product.topic ? product.topic.map(topic => <span className={styles.topicTag}>{topic}</span>) : <></>
                    }
                </div>
            </div>
            <div className={styles.productTopic}>
                <div className={styles.productTopicHeader}>
                    <h2>Techs</h2>
                </div>
                <div className={styles.productTopicBody}>
                    {
                        !!product.skill ? product.skill.map(topic => <span className={styles.topicTag}>{topic}</span>) : <></>
                    }
                </div>
            </div>
        </div>
    )
}

export default ProductItemDetail