import React, { useRef, useState, useEffect } from "react";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  ResponsiveContainer
} from "recharts";
import styles from "./index.module.css";
import { GithubAction } from "../../actions";

import GithubLogo from "../../img/github.png"

const SkillChart = () => {
  const hackingFbi = useRef(null);
  const [chartData, setChartData] = useState([]);

  useEffect(() => {
    (async () => {
      try {
        const data = await GithubAction.analyzeUserRepositories("formysister");
        setChartData(data);
      } catch (error) {
        console.error(error);
      }
    })();
  }, []);

  return (
    <div
      // onMouseDown={(e) => {
      //   simpleDragAndDrop(hackingFbi.current, e);
      // }}
      className={styles.hackingFbi}
      ref={hackingFbi}
    >
      <div className={styles.overlayLabel}>
        <h1>SKILLS</h1>
        <img src={GithubLogo} alt="Github Logo" width={80} height={80} />
      </div>
      <ResponsiveContainer width={600} height={400}>
        <BarChart data={chartData}>
          <CartesianGrid
            strokeDasharray="3 3"
            fontSize={30}
            stroke="rgba(198, 234, 252, 0.7)" 
          />
          <XAxis  label={{ value: 'random text', position: 'insideBottomRight', offset: -20 }} stroke="rgba(198, 234, 252, 0.7)"  fontSize={15} dataKey="language" />
          <YAxis stroke="rgba(198, 234, 252, 0.7)" />
          <Bar dataKey="percentage" fill="rgba(198, 234, 252, 0.7)" fillOpacity={0.7} />
        </BarChart>
      </ResponsiveContainer>
    </div>
  );
};

export default SkillChart;
