// ** import external libraries
import axios from "axios";

class GithubAction {
    /**
     *
     * @param {*} user
     * @returns {Array<Object>} user repositories
     */
    static getUserRepositories = async (user) => {
        try {
            const response = await axios.get(
                `https://api.github.com/users/${user}/repos`
            );

            return response.data;
        } catch (error) {
            throw error;
        }
    };

    /**
     *
     * @param {*} user
     * @returns {Array<Array<String>>} user language data
     */
    static analyzeUserRepositories = async (user) => {
        try {
            const response = await axios.get(
                `https://api.github.com/users/${user}/repos`
            );
            const repositories = response.data;

            // Analyze language usage
            const languages = {};
            repositories.forEach((repo) => {
                const language = repo.language;
                if (language) {
                    if (languages[language]) {
                        languages[language]++;
                    } else {
                        languages[language] = 1;
                    }
                }
            });

            // Calculate language usage percentages
            const totalRepositories = repositories.length;
            const languagePercentages = {};
            for (const language in languages) {
                const count = languages[language];
                const percentage = (count / totalRepositories) * 100;
                languagePercentages[language] = percentage.toFixed(2);
            }

            // Sort languages by usage percentage
            const sortedLanguages = Object.entries(languagePercentages);

            const formattedData = sortedLanguages.map(([language, percentage]) => {
                return { language: language, percentage: Number(percentage) };
            });

            return formattedData;
        } catch (error) {
            throw error;
        }
    };
}

export default GithubAction;
