import React, { useRef, useEffect, useState } from "react";
import styles from "./index.module.css";
import PropTypes from "prop-types";
import { GithubAction } from "../../actions";
import { simpleDragAndDrop } from "../../utils";
import data from "../../data.json";

const Projects = props => {
  const { dndPermission } = { ...props };
  const projectsWrapper = useRef(null);
  const [projects, setProjects] = useState([])


  useEffect(() => {
    (async () => {
      try {
        const repositories = await GithubAction.getUserRepositories('formysister')
        const projects = repositories.map(repository => { return { name: repository.name, href: repository.html_url } })

        setProjects(projects)
      }
      catch {
        setProjects(data.projects)
      }
    })()
  }, [])

  return (
    <div className={styles.projectsWrapper} ref={projectsWrapper}>
      <div
        className={styles.projectsBorderTop}
        onMouseDown={e => {
          if(window.innerWidth >= 976) {
            simpleDragAndDrop(projectsWrapper.current, e, dndPermission);
          }
        }}
      />
      <div className={styles.projects}>
        <div
          className={styles.projectsText}
          onMouseDown={e => {
            if(window.innerWidth >= 976) {
              simpleDragAndDrop(projectsWrapper.current, e, dndPermission);
            }
          }}
        >
          STORM OPENSOURCE
        </div>
        <div className={styles.projectsList}>
          {projects.map((element, index) => {
            return (
              <a
                href={element.href}
                target="_blank"
                rel="noopener noreferrer"
                className={styles.project}
                key={index}
              >
                {element.name}
              </a>
            );
          })}
        </div>
      </div>
    </div>
  );
};

Projects.propTypes = {
  dndPermission: PropTypes.string.isRequired
};

export default Projects;
